.stat-card {
  border: 0.0625rem solid #efefef;
  background-color: #fff;
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  padding: 1.25rem;
  transition: all 0.3s ease-in-out;
  display: flex; }
  .stat-card.span-2 {
    grid-column-end: span 2; }
    .stat-card.span-2 .main-content {
      width: 50%; }
  .stat-card.span-3 {
    grid-column-end: span 3; }
    .stat-card.span-3 .main-content  {
      width: 33%; }
  .stat-card.span-4 {
    grid-column-end: span 4; }
    .stat-card.span-4 .main-content {
      width: 25%; }
  .stat-card:hover {
    box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.25); }
  .stat-card .main-content {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    max-width: 100%; }
    .stat-card .main-content .card-title {
      display: block;
      font-weight: bold;
      color: #2C2A2F;
      font-size: 1rem;
      margin: 0 0 0.625rem; }
    .stat-card .main-content .card-description {
      display: block;
      color: #45424a;
      font-size: 0.6875rem;
      margin: 0 0 0.625rem; }

.courses-list {
  padding: 1.5625rem 0;
  border-top: 1px solid #ccc; }

.course-list-item {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 1.25rem;
  border-bottom: 1px solid #ccc;
  padding: 1.25rem 0;
  font-size: 0.875rem;
  align-items: center; }
  @media (max-width: 39.9375rem) {
    .course-list-item {
      grid-template-columns: 1fr;
      grid-row-gap: 0.9375rem; } }
  .course-list-item.list-header {
    font-size: 0.75rem; }
  .course-list-item .sorting-header-button {
    padding: 0;
    margin: 0;
    background: none;
    border: none;
    outline: none; }
    .course-list-item .sorting-header-button span {
      margin-right: 0.3125rem;
      text-decoration: underline;
      font-size: 0.75rem; }
    .course-list-item .sorting-header-button:hover, .course-list-item .sorting-header-button:focus {
      cursor: pointer; }
  .course-list-item .course-name-link {
    color: #0090c1;
    font-weight: bold;
    text-decoration: none; }
  .course-list-item .course-id {
    font-size: 0.75rem; }
  .course-list-item .start-date, .course-list-item .self-paced, .course-list-item .enrolments, .course-list-item .completions {
    text-align: center; }
    @media (max-width: 39.9375rem) {
      .course-list-item .start-date, .course-list-item .self-paced, .course-list-item .enrolments, .course-list-item .completions {
        text-align: left; } }
  .course-list-item .action-container {
    text-align: right; }
    @media (max-width: 39.9375rem) {
      .course-list-item .action-container {
        text-align: left;
        padding-top: 0.9375rem; } }
  .course-list-item .course-action {
    font-size: 0.875rem;
    padding: 0.5rem 0.9375rem;
    font-weight: normal;
    color: #0090c1;
    border: 0.0625rem solid #0090c1;
    background: transparent;
    cursor: pointer;
    border-radius: 1.875rem;
    transition: all 0.3s ease-in-out;
    text-decoration: none; }
    .course-list-item .course-action:hover {
      color: #fff;
      background: #0090c1; }
    @media (max-width: 39.9375rem) {
      .course-list-item .course-action {
        display: inline-block;
        min-width: 12.5rem;
        text-align: center; } }

.in-cell-label-value {
  display: flex;
  align-items: baseline; }
  .in-cell-label-value .mobile-label {
    flex-grow: 0;
    flex-shrink: 0;
    width: 5.625rem;
    margin-right: 0.9375rem;
    font-size: 0.75rem;
    color: #666; }
    @media (min-width: 39.9375rem) {
      .in-cell-label-value .mobile-label {
        display: none; } }
  .in-cell-label-value .mobile-value {
    flex-grow: 1;
    flex-shrink: 1;
