.header-nav {
  margin-left: auto;
  display: flex;
  align-items: center; }

a.header-nav__link, a.header-nav__link:not(.btn) {
  margin-right: 1.875rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: #fff !important;
  text-decoration: none;
  position: relative; }
  a.header-nav__link:after, a.header-nav__link:not(.btn):after {
    position: absolute;
    bottom: -0.625rem;
    height: 0.125rem;
    width: 0;
    left: 0;
    background-color: #fff;
    content: '';
    transition: all 0.2s ease-in-out; }
  a.header-nav__link:hover, a.header-nav__link:not(.btn):hover {
    color: #fff; }
    a.header-nav__link:hover:after, a.header-nav__link:not(.btn):hover:after {
      width: 100%; }
  a.header-nav__link:focus, a.header-nav__link:not(.btn):focus {
    color: #fff; }
  a.header-nav__link.active, a.header-nav__link:not(.btn).active {
    font-weight: 900;
    opacity: 0.6; }



