.stat-card {
  border: 0.0625rem solid #efefef;
  background-color: #fff;
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  padding: 1.25rem;
  transition: all 0.3s ease-in-out;
  display: flex; }
  .stat-card.span-2 {
    grid-column-end: span 2; }
    .stat-card.span-2 .main-content {
      width: 50%; }
  .stat-card.span-3 {
    grid-column-end: span 3; }
    .stat-card.span-3 .main-content  {
      width: 33%; }
  .stat-card.span-4 {
    grid-column-end: span 4; }
    .stat-card.span-4 .main-content {
      width: 25%; }
  .stat-card:hover {
    box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.25); }
  .stat-card .main-content {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    max-width: 100%; }
    .stat-card .main-content .card-title {
      display: block;
      font-weight: bold;
      color: #2C2A2F;
      font-size: 1rem;
      margin: 0 0 0.625rem; }
    .stat-card .main-content .card-description {
      display: block;
      color: #45424a;
      font-size: 0.6875rem;
      margin: 0 0 0.625rem; }

.course-quick-links {
  display: flex;
  align-items: center;
  padding-bottom: 1.875rem; }
  .course-quick-links__line {
    display: block;
    flex: 1 1 100%;
    height: 1px;
    background-color: #ccc; }
  .course-quick-links__link, .course-quick-links__link:not(.btn), .course-quick-links__link:visited:not(.btn) {
    display: inline-block;
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
    color: #0090c1;
    background: none;
    border: 2px solid #0090c1;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    border-radius: 3.125rem;
    font-weight: bold;
    margin-left: 1.25rem;
    flex-shrink: 0;
    flex-grow: 0; }
    .course-quick-links__link:hover, .course-quick-links__link:not(.btn):hover, .course-quick-links__link:visited:not(.btn):hover {
      background-color: #0090c1;
      color: #fff;
      cursor: pointer; }



