.stat-card {
  border: 0.0625rem solid #efefef;
  background-color: #fff;
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  padding: 1.25rem;
  transition: all 0.3s ease-in-out;
  display: flex; }
  .stat-card.span-2 {
    grid-column-end: span 2; }
    .stat-card.span-2 .main-content {
      width: 50%; }
  .stat-card.span-3 {
    grid-column-end: span 3; }
    .stat-card.span-3 .main-content  {
      width: 33%; }
  .stat-card.span-4 {
    grid-column-end: span 4; }
    .stat-card.span-4 .main-content {
      width: 25%; }
  .stat-card:hover {
    box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.25); }
  .stat-card .main-content {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    max-width: 100%; }
    .stat-card .main-content .card-title {
      display: block;
      font-weight: bold;
      color: #2C2A2F;
      font-size: 1rem;
      margin: 0 0 0.625rem; }
    .stat-card .main-content .card-description {
      display: block;
      color: #45424a;
      font-size: 0.6875rem;
      margin: 0 0 0.625rem; }

.user-courses-list {
  grid-column: span 3;
  padding-left: 1.875rem; }
  @media (max-width: 63.75rem) {
    .user-courses-list {
      grid-column: span 1;
      padding-right: 1.875rem; } }

.course-item {
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  padding-bottom: 1.75rem;
  margin-bottom: 1.25rem; }
  @media (max-width: 63.75rem) {
    .course-item {
      display: block;
      width: auto; } }
  .course-item .course-info {
    width: 15.625rem;
    padding-right: 1.25rem;
    margin-right: 1.25rem;
    border-right: 1px solid #dfdde1;
    max-width: 100%;
    flex-shrink: 0;
    flex-grow: 0; }
    @media (max-width: 63.75rem) {
      .course-item .course-info {
        width: 100%;
        border-right: none;
        border-bottom: 1px solid #dfdde1;
        margin-right: 0;
        margin-bottom: 1.25rem;
        padding-bottom: 1.25rem; } }
    .course-item .course-info .course-code {
      display: block;
      font-size: 0.875rem;
      font-weight: bold;
      color: #0090c1;
      margin-bottom: 0.3125rem; }
    .course-item .course-info .course-name {
      display: block;
      font-size: 1.125rem;
      font-weight: bold;
      color: #2C2A2F;
      margin-bottom: 0.625rem;
      line-height: 130%; }
  .course-item .user-stats {
    padding: 0;
    margin: 0;
    display: flex;
    width: 100%;
    flex-grow: 1;
    flex-shrink: 1; }
    .course-item .user-stats .stat {
      flex-grow: 0;
      flex-shrink: 0;
      width: 25%;
      list-style-type: none;
      text-align: center; }
      .course-item .user-stats .stat .stat-label {
        display: block;
        width: 100%;
        margin-bottom: 0.625rem;
        font-size: 0.75rem;
        color: #787280; }
      .course-item .user-stats .stat .stat-value {
        font-size: 0.875rem;
        display: block; }
        .course-item .user-stats .stat .stat-value .completed-icon {
          color: #27ae60; }
  .course-item .progress-bar {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.5rem;
    background-color: #eee; }
    .course-item .progress-bar .bar {
      background-color: #0090c1;
      display: block;
      height: 0.5rem; }
      .course-item .progress-bar .bar.finished  {
        background-color: #27ae60; }



