.container {
  width: 100%;
  max-width: 73.75rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  box-sizing: border-box; }

.layout-root {
  width: 100%;
  padding: 0;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased; }

.base-grid-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.25rem;
  grid-auto-flow: dense; }
  @media (max-width: 63.75rem) {
    .base-grid-layout {
      grid-template-columns: repeat(2, 1fr); } }

.grid-element--full-width {
  grid-column-end: span 4; }

.grid-element--three-quarter {
  grid-column-end: span 3; }
  @media (max-width: 63.75rem) {
    .grid-element--three-quarter {
      grid-column-end: span 4; } }

.grid-element--one-half {
  grid-column-end: span 2; }
  @media (max-width: 26.25rem) {
    .grid-element--one-half {
      grid-column-end: span 4; } }

.grid-element--one-quarter {
  grid-column-end: span 1; }
  @media (max-width: 63.75rem) {
    .grid-element--one-quarter {
      grid-column-end: span 2; } }
  @media (max-width: 26.25rem) {
    .grid-element--one-quarter {
      grid-column-end: span 4; } }

:global(.page-leave) {
  opacity: 1;
  transition: all 0.4s ease-in-out; }

:global(.page-leave.page-leave-active) {
  opacity: 0;
  transition: opacity .4s ease-in; }

:global(.page-enter) {
  opacity: 0; }

:global(.page-enter.page-enter-active) {
  opacity: 1;
  /* Delay the enter animation until the leave completes */
  transition: opacity .4s ease-in .6s; }

:global(.page-height) {
  transition: height .6s ease-in-out; }

.dashboard-content {
  margin-top: 2.5rem;
  padding-bottom: 3.125rem; }

.functionality-callout {
  text-align: center;
  border-top: 1px solid #ccc;
  margin-top: 1.875rem;
  padding-top: 3.75rem; }
  .functionality-callout h3 {
    font-size: 1.25rem;
    text-align: center;
    max-width: 50rem;
    margin: 0 auto; }
  .functionality-callout .functionality-callout-cta {
    display: inline-block;
    font-size: 1rem;
    padding: 0.9375rem 1.5625rem;
    margin: 1.875rem auto 3.125rem;
    font-weight: bold;
    color: #0090c1 !important;
    border: 0.125rem solid #0090c1;
    background: transparent;
    cursor: pointer;
    border-radius: 1.875rem;
    transition: all 0.3s ease-in-out;
    text-decoration: none; }
    .functionality-callout .functionality-callout-cta:hover {
      color: #fff !important;
      background: #0090c1; }



