.stat-card {
  border: 0.0625rem solid #efefef;
  background-color: #fff;
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.25);
  padding: 1.25rem;
  transition: all 0.3s ease-in-out;
  display: flex; }
  .stat-card.span-2 {
    grid-column-end: span 2; }
    .stat-card.span-2 .main-content {
      width: 50%; }
  .stat-card.span-3 {
    grid-column-end: span 3; }
    .stat-card.span-3 .main-content  {
      width: 33%; }
  .stat-card.span-4 {
    grid-column-end: span 4; }
    .stat-card.span-4 .main-content {
      width: 25%; }
  .stat-card:hover {
    box-shadow: 0 0.375rem 0.75rem 0 rgba(0, 0, 0, 0.25); }
  .stat-card .main-content {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    flex-grow: 0;
    max-width: 100%; }
    .stat-card .main-content .card-title {
      display: block;
      font-weight: bold;
      color: #2C2A2F;
      font-size: 1rem;
      margin: 0 0 0.625rem; }
    .stat-card .main-content .card-description {
      display: block;
      color: #45424a;
      font-size: 0.6875rem;
      margin: 0 0 0.625rem; }

.stat-card .main-content .main-data-container {
  display: flex;
  align-items: center;
  color: #0090c1;
  padding: 1.25rem 0;
  margin-top: auto;
  min-height: 5.625rem;
  box-sizing: border-box; }
  .stat-card .main-content .main-data-container .current-data {
    font-weight: 900;
    font-size: 2rem;
    line-height: 100%;
    height: 100%; }
  .stat-card .main-content .main-data-container .previous-comparison {
    font-weight: normal;
    margin-left: 0.9375rem;
    padding: 0.3125rem 0 0.3125rem 0.9375rem;
    border-left: 0.0625rem solid #0090c1; }
  .stat-card .main-content .main-data-container .comparison-value {
    font-weight: bold;
    font-size: 1.125rem;
    display: block; }
  .stat-card .main-content .main-data-container .comparison-text {
    font-size: 0.75rem;
    display: block; }

.stat-card .main-content .history-toggle {
  background: none;
  text-shadow: none;
  font: inherit;
  text-transform: none;
  letter-spacing: normal;
  color: #aba7b1;
  font-size: 0.875rem;
  font-weight: bold;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  padding: 0;
  text-align: left; }
  .stat-card .main-content .history-toggle:hover {
    color: #0090c1; }

.stat-card .main-content .history-toggle-faux {
  height: 1.1875rem; }

.stat-card .history-content {
  flex-shrink: 1;
  flex-grow: 1; }



