.container {
  width: 100%;
  max-width: 73.75rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  box-sizing: border-box; }

.layout-root {
  width: 100%;
  padding: 0;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased; }

.base-grid-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.25rem;
  grid-auto-flow: dense; }
  @media (max-width: 63.75rem) {
    .base-grid-layout {
      grid-template-columns: repeat(2, 1fr); } }

.grid-element--full-width {
  grid-column-end: span 4; }

.grid-element--three-quarter {
  grid-column-end: span 3; }
  @media (max-width: 63.75rem) {
    .grid-element--three-quarter {
      grid-column-end: span 4; } }

.grid-element--one-half {
  grid-column-end: span 2; }
  @media (max-width: 26.25rem) {
    .grid-element--one-half {
      grid-column-end: span 4; } }

.grid-element--one-quarter {
  grid-column-end: span 1; }
  @media (max-width: 63.75rem) {
    .grid-element--one-quarter {
      grid-column-end: span 2; } }
  @media (max-width: 26.25rem) {
    .grid-element--one-quarter {
      grid-column-end: span 4; } }

:global(.page-leave) {
  opacity: 1;
  transition: all 0.4s ease-in-out; }

:global(.page-leave.page-leave-active) {
  opacity: 0;
  transition: opacity .4s ease-in; }

:global(.page-enter) {
  opacity: 0; }

:global(.page-enter.page-enter-active) {
  opacity: 1;
  /* Delay the enter animation until the leave completes */
  transition: opacity .4s ease-in .6s; }

:global(.page-height) {
  transition: height .6s ease-in-out; }

.header-content-static {
  margin-bottom: 3.75rem;
  position: relative;
  padding-bottom: 2.5rem; }
  .header-content-static .title {
    font-weight: 900;
    color: #fff;
    font-size: 3rem;
    line-height: 120%;
    margin: 1.25rem 0 0.625rem; }
  .header-content-static .subtitle {
    font-weight: normal;
    color: #fff;
    font-size: 1.125rem;
    line-height: 150%; }



